<script>
	import "css-doodle";

	import Loader from "./page/loader.svelte";
	import index from "./page/index.svelte";
	import foreword from "./page/foreword.svelte";
	import explore from "./page/explore.svelte";
	import battle from "./page/battle.svelte";
	import afflatus from "./page/afflatus.svelte";

	import { page, scene, ctrl, loading } from "./store";

	const render = {
		index,
		foreword,
		explore,
		battle,
		afflatus
	};

	// $ctrl = {
	// 	page: (p) => {
	// 		$page = "loading";
	// 		$loading = true;
	// 		setTimeout((_) => ($page = p), 300);
	// 	},
	// };

	window.deepCopy = (obj) => {
		if (Array.isArray(obj)) {
			let arr = obj;
			var newArr = [];
			for (var i = 0; i < arr.length; i++) {
				if (Array.isArray(arr[i])) {
					var list = deepCopy(arr[i]);
					newArr.push(list);
				} else {
					newArr.push(arr[i]);
				}
			}
			return newArr;
		} else {
			let temp = obj.constructor === Array ? [] : {};
			for (let val in obj) {
				temp[val] = typeof obj[val] == "object" ? deepCopy(obj[val]) : obj[val];
			}
			return temp;
		}
	};
	Array.prototype.rd = function () {
		for (
			var j, x, i = this.length;
			i;
			j = parseInt(Math.random() * i),
				x = this[--i],
				this[i] = this[j],
				this[j] = x
		);
		return this;
	};
	String.prototype.getByteLen = function () {
		let len = 0;
		for (let i = 0; i < this.length; i++) {
			this.charCodeAt(i) < 256 ? (len += 1) : (len += 2);
		}
		return len;
	};
	window.r = Math.random;
	class RAF {
		constructor() {
			this.init();
		}
		init() {
			this._timerIdMap = {
				timeout: {},
				interval: {},
			};
		}
		run(type = "interval", cb, interval = 16.7) {
			const now = Date.now;
			let stime = now();
			let etime = stime;
			//创建Symbol类型作为key值，保证返回值的唯一性，用于清除定时器使用
			const timerSymbol = Symbol();
			const loop = () => {
				this.setIdMap(timerSymbol, type, loop);
				etime = now();
				if (etime - stime >= interval) {
					if (type === "interval") {
						stime = now();
						etime = stime;
					}
					cb();
					type === "timeout" && this.clearTimeout(timerSymbol);
				}
			};
			this.setIdMap(timerSymbol, type, loop);
			return timerSymbol; // 返回Symbol保证每次调用setTimeout/setInterval返回值的唯一性
		}
		setIdMap(timerSymbol, type, loop) {
			const id = requestAnimationFrame(loop);
			this._timerIdMap[type][timerSymbol] = id;
		}
		setTimeout(cb, interval) {
			// 实现setTimeout 功能
			return this.run("timeout", cb, interval);
		}
		clearTimeout(timer) {
			cancelAnimationFrame(this._timerIdMap.timeout[timer]);
		}
		setInterval(cb, interval) {
			// 实现setInterval功能
			return this.run("interval", cb, interval);
		}
		clearInterval(timer) {
			cancelAnimationFrame(this._timerIdMap.interval[timer]);
		}
	}
	window.RAF = RAF;
</script>

<div id="root" style="background-image:url(/img/scene/{$scene}.webp)">
	<svelte:component this={render[$page]} />
	<Loader />
</div>

<style>
	#root {
		height: 100%;
		width: 100%;
		align-items: center;
		justify-content: center;
		background-size: cover;
		background-position: center;
		overflow: hidden;
	}
</style>
